export class Governance_model {

	constructor(){

	}

	fetch_anchor_tags = ()=>{

		if(process.env.NODE_ENV === "production"){

			return fetch('https://www.abbeyroad.org.uk/api/governance_page/fetch_anchor_tags')
			.then((res)=>{
				return res.json()
			})
			.then((res)=>{
				console.log(res)
				if(res.success && res.elements){
					return Promise.resolve({
						elements: res.elements
					})
				}
				else{
					console.log("error getting anchor tags1")
					return Promise.reject()
				}
			})
			.catch((err)=>{
				console.log("error getting anchor tags2")
				return Promise.reject()
			})
		}

		else {

			return fetch('http://localhost:3001/api/governance_page/fetch_anchor_tags')
			.then((res)=>{
				return res.json()
			})
			.then((res)=>{
	
				if(res.success && res.elements){
					return Promise.resolve({
						elements: res.elements
					})
				}
				else{
					console.log("error getting anchor tags1")
					return Promise.reject()
				}
			})
			.catch((err)=>{
				console.log("error getting anchor tags2")
				return Promise.reject()
			})
		}
	}
}