export class Governance_view {

	constructor(){

	}

	open_and_close_navbar = () => {

		const navbar_btn_open = 
		document.getElementById("mobile-menu-btn")

		const navbar_btn_close = 
		document.getElementById("mobile-menu-close")

		const menu = document.getElementById("mobile-menu");

		navbar_btn_open.addEventListener("click", (event)=>{

			menu.style.display = 'block';
		})

		navbar_btn_close.addEventListener("click", (event)=>{
			menu.style.display = 'none';
		})
	}

	render_anchor_tags = (elements) => {

		const ul = document.getElementById("governance-main-article-ul1");
		//MOBILE
		const ul_mobile = document.getElementById("governance-mobile-ul-anchor");

		elements.forEach((element)=>{

			const li = document.createElement("li");
			const a = document.createElement("a");
			const p = document.createElement("p");
			//MOBILE
			const li_mobile = document.createElement("li");
			const a_mobile = document.createElement("a");

			li.classList.add("governance-main-article-ul-li");
			a.classList.add("governance-main-article-ul-li-a");
			a.setAttribute("target", "_blank");
			if (process.env.NODE_ENV === "production") {
			  a.setAttribute("href", element.production_url)
			} 
			else {
			  a.setAttribute("href", element.development_url)
			}
			p.classList.add("governance-main-article-ul-li-a-p");

			p.textContent = element.name

			a.appendChild(p);
			li.appendChild(a);
			ul.appendChild(li)
			//MOBILE

			a_mobile.setAttribute("target", "_blank");
			if (process.env.NODE_ENV === "production") {
			  a_mobile.setAttribute("href", element.production_url)
			} 
			else {
			  a_mobile.setAttribute("href", element.development_url)
			}

			a_mobile.textContent = element.name

			ul_mobile.appendChild(li_mobile);
			li_mobile.appendChild(a_mobile);

		})
	}
}